@im .text-field-container {
  width: 100%;
  position: relative;
  display: flex;
}
.icon-container {
  position: absolute;
}

.label-container {
  font-size: 12px;
  color: var(--text-secondary-text-default);
}

.input,
.input--light-theme {
  background: transparent;
  border-radius: 4px;
  border: 1px solid var(--component-component-secondary);
  padding-left: 12px;
  padding-right: 12px;
  white-space: nowrap;
  width: 100%;
  height: 24px;
  box-sizing: border-box;
  color: var(--text-base-text-default);
  font-family: Rubik;
  font-size: 14px;
}

.input--light-theme:focus {
  background: transparent;
  outline: none;
  border: 1px solid var(--state-active);
}

.input--disabled--light-theme {
  pointer-events: none;
  color: var(--state-disabled);
  border: 1px solid var(--state-disabled);
}

.input--dark-theme {
  border: 1px solid var(--component-component-secondary--dark);
  color: var(--text-base-text-default--dark);
}

.input--dark-theme:focus {
  background: transparent;
  outline: none;
  border: 1px solid var(--state-active--dark);
}

